<template>
  <div class="card">
    <div class="card-header">
      Exam02CounterState
    </div>
    <div class="card-body">
      <h6>[Counter State 읽기]</h6>
      <p>counter/count 단방향 바인딩: {{$store.state.counter.count}} </p>
      <p>counter/count 단방향 바인딩: {{$store.getters["counter/getCount"]}} </p>
      <p>counter/count 단방향 바인딩: {{getCount()}} </p>
      <p>counter/count 단방향 바인딩: {{computedCount}} </p>
      <p>counter/count 양방향 바인딩: <input type="text" v-model.number="$store.state.counter.count"/></p>

      <hr/>

      <h6>[Counter State 변경]</h6>
      <p>count 증가값 입력: <input type="text" v-model.number="value"/></p>
      <div class="mt-2">
        <button @click="changeCountByMutation" class="btn btn-info btn-sm mt-1 mr-2">
          counter/count 변경(Mutation 동기 방식)</button>
        <button @click="changeCountByAction" class="btn btn-info btn-sm mt-1">
          counter/count 변경(Action 비동기 방식)</button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { useStore } from "vuex";
import { computed } from "vue";

const store = useStore();

function getCount() {
  return store.state.counter.count;
  //return store.getters["counter/getCount"];
}

const computedCount = computed(() => {
  //return store.state.counter.count;
  return store.getters["counter/getCount"];
});

import { ref } from "vue";
const value = ref(1);

function changeCountByMutation() {
  store.commit("counter/setCount", value.value);
}

function changeCountByAction() {
  store.dispatch("counter/setCountByAsync", { duration:3000, value:value.value});
}
</script>

